import * as THREE from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { PointerLockControls } from "three/examples/jsm/controls/PointerLockControls.js";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader";
import { manager, isMobile } from "./manage.js";

import * as dat from 'dat.gui'

let scene, camera, renderer, control, light, playerCamera, orbitCamera;
const CONTROLS = { orbit: undefined, lock: undefined };   // 
export const cameraMove = { W: false, A: false, S: false, D: false, Space: false, shift: false, E: false };  // 
const groundMaterials = { phong: undefined, standard: undefined };  // 
const gui = new dat.GUI();
const guiParameters = { isPhong: true, isFirst: false };  // gui
const islandRange = { min_x: 38, max_x: 56, min_z: -7, max_z: 11 }; // 
const waterRange = { min_x: 19, max_x: 41, min_z: -9, max_z: 12 }; // 
let isMaterialChangeClick = false;
let isControlChangeClick = false;
let groundObj;  // 
let playerGroup = new THREE.Group();
let playerSpeed = 6

function init() {
  scene = new THREE.Scene();
  orbitCamera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
  camera = orbitCamera
  renderer = new THREE.WebGLRenderer({ antialias: true }); // 
  renderer.setSize(window.innerWidth, window.innerHeight);
  document.body.appendChild(renderer.domElement);

  isMobile() ? camera.position.set(2, 2, 8) : camera.position.set(2, 1, 10);

  // 
  light = new THREE.DirectionalLight(0xffffff, 1);
  light.position.set(-1, 2, -2)
  const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
  scene.add(light);
  scene.add(ambientLight);

  // 
  light.castShadow = true;
  renderer.shadowMap.enabled = true;
  renderer.shadowMap.type = THREE.PCFSoftShadowMap;

  // 
  CONTROLS.orbit = new OrbitControls(camera, renderer.domElement);
  CONTROLS.lock = new PointerLockControls(playerGroup, renderer.domElement);
  control = CONTROLS.orbit;
  control.enableDamping = true;
  
  if (isMobile()) {
    control.enablePan = false
    control.maxDistance = 10
    control.minDistance = 2
  }

  createCanvasSprites()
  initObj()
  initPlayer()
}
init();

function initPlayerObj() {
  const sphereGeo = new THREE.SphereGeometry(0.05)
  const sphereMat = new THREE.MeshBasicMaterial({color: 0xffffff})
  const sphere = new THREE.Mesh(sphereGeo, sphereMat)
  const pointLight = new THREE.PointLight(0xff6232c4, 0.9, 10)
  const torusGeometry = new THREE.TorusGeometry( 0.1, 0.01, 16, 100 );
  const torusMaterial = new THREE.MeshBasicMaterial( { color: 0xffffff } );
  const torus = new THREE.Mesh( torusGeometry, torusMaterial );
  return { sphere, pointLight, torus }
}

function initPlayer() {
  // group
  playerCamera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
  const { sphere, pointLight, torus } = initPlayerObj()
  playerGroup.add(torus);
  playerGroup.add(pointLight)
  playerGroup.add(sphere)
  !isMobile() && playerGroup.add(playerCamera)
  
  sphere.position.set(0, 0, 0);
  playerCamera.position.set(0, 1, 5);
  playerGroup.position.set(2, 1, 5);
  if (isMobile()) scene.add(playerGroup)
}

function initObj() {
  const fbxLoader = new FBXLoader(manager);
  const skysImg = {
    top: require("./assets/skybox/top.png"),
    bottom: require("./assets/skybox/bottom.png"),
    left: require("./assets/skybox/left.png"),
    right: require("./assets/skybox/right.png"),
    front: require("./assets/skybox/front.png"),
    back: require("./assets/skybox/back.png"),
  }
  const skyTexture = new THREE.CubeTextureLoader().load([
    skysImg.right,
    skysImg.left,
    skysImg.top,
    skysImg.bottom,
    skysImg.front,
    skysImg.back,
  ]);
  scene.background = skyTexture;

  const islandPath = require("./assets/Low Poly Island.fbx");
  const groups = {
    // Ground_01: require("./assets/Models/Ground_01.fbx"),
    // Ground_02: require("./assets/Models/Ground_02.fbx"),
    Ground_03: require("./assets/Models/Ground_03.fbx"),

    park: require("./assets/park.fbx"),
  }
  let waterMat;
  // 
  fbxLoader.load(islandPath, model => {
    const idx = [];
    for (let i = 0; i < model.children.length; i++) {
      const e = model.children[i];
      e.castShadow = true;
      e.receiveShadow = true;
      if (e.name == "Light" || e.name == "Camera") idx.push(i);
      if (e.name == "Plane") {
        waterMat = e.material;
      } else {
        if (e.material && e.material.length && e.material.length > 0) {
          for (let j = 0; j < e.material.length; j++) {
            const m = e.material[j];
            m.shininess = 30;
            m.specular.r = 0.06;
            m.specular.g = 0.06;
            m.specular.b = 0.06;
          }
        }
        if (e.material && !e.material.length) {
          e.material.shininess = 30;
          e.material.specular.r = 0.06
          e.material.specular.g = 0.06
          e.material.specular.b = 0.06
        }
      }
    }
    for (let i = 0; i < idx.length; i++) {
      model.children.splice(idx[i] - i, 1);
    }
    model.scale.set(0.01, 0.01, 0.01);
    model.rotateY(Math.PI/2.5);
    model.position.set(47, 10, 2);
    scene.add(model);
  })

  // 
  fbxLoader.load(groups.Ground_03, obj => {
    obj.scale.set(0.7, 0.7, 0.7);
    obj.rotateY(Math.PI / 2.12)
    obj.position.set(30, 0, 2);
    obj.children[0].material = waterMat[1]
    obj.children[0].material.envMap = skyTexture;
    scene.add(obj)
  })

  // 
  const texturePath = require("../textures/NaturePackLite_Texture_01.png");
  fbxLoader.load(groups.park, obj => {
    const texture = new THREE.TextureLoader(manager).load(texturePath);
    groundMaterials.phong = new THREE.MeshPhongMaterial({ map: texture, envMap: skyTexture, shininess: 30 });
    groundMaterials.standard = new THREE.MeshStandardMaterial({ map: texture, envMap: skyTexture });
    const material = guiParameters.isPhong ? groundMaterials.phong : groundMaterials.standard;
    obj.traverse(mesh => {
      if(mesh.name.includes("Ground")) {
        mesh.receiveShadow = true;
      } else {
        mesh.castShadow = true;
      }
      mesh.material = material;
    })
    obj.scale.set(0.01, 0.01, 0.01);
    obj.position.y = 1.7;
    obj.receiveShadow = true;
    groundObj = obj;
    scene.add(obj)
  })

  
}


gui.add(guiParameters, 'isPhong').name("BlinnPhong").onChange(() => { isMaterialChangeClick = true });
!isMobile() && gui.add(guiParameters, "isFirst").name("+").onChange(v => { 
  isControlChangeClick = true;
  if (v) {
    camera = playerCamera
    scene.add(playerGroup)
    !document.fullscreenElement && document.documentElement.requestFullscreen();
  } else {
    camera = orbitCamera
    scene.remove(playerGroup)
  }
})

// gui
function onControlChange() {
  if (!isControlChangeClick) return
  if (guiParameters.isFirst) {
    control = CONTROLS.lock;
    control.lock();
  } else {
    control = CONTROLS.orbit;
    control.enableDamping = true
  }
  isControlChangeClick = false;
}
// gui
function onMaterialChange() {
  if (!isMaterialChangeClick) return;
  if (groundObj && (groundMaterials.phong || groundMaterials.standard)) {
    groundObj.traverse(child => {
      child.material = guiParameters.isPhong ? groundMaterials.phong : groundMaterials.standard
    })
    light.intensity = guiParameters.isPhong ? 0.7 : 1;
  }
  isMaterialChangeClick = false
}
// player
let isColorChange = false
function playerAnimate() {
  if (!isMobile() && !guiParameters.isFirst) return;
  playerGroup.children[0].rotation.x += 0.03
  playerGroup.children[0].rotation.y += 0.05
  playerGroup.children[0].rotation.z += 0.03
  
  const isOnIsland = 
    playerGroup.position.x <= islandRange.max_x && 
    playerGroup.position.x >= islandRange.min_x && 
    playerGroup.position.z <= islandRange.max_z && 
    playerGroup.position.z >= islandRange.min_z;
  const isInWater = 
    playerGroup.position.x <= waterRange.max_x && 
    playerGroup.position.x >= waterRange.min_x && 
    playerGroup.position.z <= waterRange.max_z && 
    playerGroup.position.z >= waterRange.min_z;
  if (isOnIsland || isInWater) {
    if (!isColorChange) {
      playerGroup.children[1].intensity = 0.5;
      playerGroup.children[2].material.color = new THREE.Color(0xd8d8d8);
      playerGroup.children[0].material.color = new THREE.Color(0xd8d8d8);
      isColorChange = true
    }
  } else {
    if (isColorChange) {
      playerGroup.children[1].intensity = 0.9;
      playerGroup.children[2].material.color = new THREE.Color(0xffffff);
      playerGroup.children[0].material.color = new THREE.Color(0xffffff);
      isColorChange = false
    }
  }
}
// 
function onCameraControlMob() {
  if(isMobile()) control.target = new THREE.Vector3(playerGroup.position.x, playerGroup.position.y+1, playerGroup.position.z)
}

function Move() {
  if (!guiParameters.isFirst) return;
  const { W, A, S, D, Space, shift, E } = cameraMove;
  let distance = 0.01 * playerSpeed
  if (shift) {distance *= 2} else {distance = 0.01 * playerSpeed}
  if (W) control.moveForward(distance);
  if (A) control.moveRight(-distance);
  if (S) control.moveForward(-distance);
  if (D) control.moveRight(distance);
  if (Space) {
    const p = Math.pow(1 / playerGroup.position.y, 0.5) + 1.5;  // 
    playerGroup.position.y += distance * p
  } else {  // 
    if (E) return;
    const islandY = 13;
    const isOnIsland = 
      playerGroup.position.x <= islandRange.max_x && 
      playerGroup.position.x >= islandRange.min_x && 
      playerGroup.position.z <= islandRange.max_z && 
      playerGroup.position.z >= islandRange.min_z;

    if (playerGroup.position.y > 1) {
      const p = Math.pow(playerGroup.position.y, 0.5) + 1.5;  // 
      if (isOnIsland) {
        if (playerGroup.position.y > islandY) {
          playerGroup.position.y -= distance * p;
        }
      } else {
        playerGroup.position.y -= distance * p;
      }
    }

  }

}

function mobilePlayerJump() {
  if (!isMobile()) return;
  let distance = 0.01
  if (cameraMove.Space) {
    const p = Math.pow(1 / playerGroup.position.y, 0.5) + 1.5;  // 
    playerGroup.position.y += distance * p
    control.object.position.y += distance * p;
  } else {
    const islandY = 13;
    const isOnIsland = 
      playerGroup.position.x <= islandRange.max_x && 
      playerGroup.position.x >= islandRange.min_x && 
      playerGroup.position.z <= islandRange.max_z && 
      playerGroup.position.z >= islandRange.min_z;

    if (playerGroup.position.y > 1) {
      const p = Math.pow(playerGroup.position.y, 0.5) + 1.5;  // 
      if (isOnIsland) {
        if (playerGroup.position.y > islandY) {
          playerGroup.position.y -= distance * p;
          control.object.position.y -= distance * p;
        }
      } else {
        playerGroup.position.y -= distance * p;
        control.object.position.y -= distance * p;
      }
    }
  }
}

function getDir(dir1, dir2) {
  return new THREE.Vector3(dir2.x - dir1.x, 0, dir2.z - dir1.z).normalize()
}
function matrixMulVector(m, v) {
  const x = m[0] * v[0] + m[1] * v[1] + m[2] * v[2]
  const y = m[3] * v[0] + m[4] * v[1] + m[5] * v[2]
  const z = m[6] * v[0] + m[7] * v[1] + m[8] * v[2]
  return [x, y, z]
} 

// 
function onPlayerMoveMob() {
  if (!isTouch) return
  let distance = 0.03
  const moveDir = new THREE.Vector3(direction[0], direction[1], direction[2]).normalize()
  const cameraDir = getDir(control.object.position, playerGroup.position)
  const front = new THREE.Vector3(0, 0, -1) // 

  const cos = cameraDir.dot(front);
  const sin = cameraDir.x > 0 ? Math.sqrt(1 - cos * cos) : -Math.sqrt(1 - cos * cos);
  const rotateMatrix = new THREE.Matrix3().set(
    cos, 0, sin,
    0, 1, 0,
    -sin, 0, cos
  );
  console.log(playerGroup.position.x - camera.position.x, 0, playerGroup.position.z - camera.position.z);
  
  // 
  const dir = matrixMulVector(rotateMatrix.toArray(), [moveDir.x, moveDir.y, moveDir.z])

  playerGroup.position.x += distance * dir[0]
  playerGroup.position.z += distance * dir[2]
  control.object.position.x += distance * dir[0]
  control.object.position.z += distance * dir[2]
}

function animate(time) {
  // const second = Math.floor(time/1000);
  mobilePlayerJump()
  onPlayerMoveMob()
  onCameraControlMob()
  playerAnimate()
  onMaterialChange();
  onControlChange();
  document.fullscreenElement ? gui.hide() : gui.show();
  !guiParameters.isFirst ? control.update() : Move();
}

const tick = (time) => {
  animate(time);
  renderer.render(scene, camera);
  requestAnimationFrame(tick);
}
tick()

const inputs = document.getElementsByTagName("input")
// 
window.addEventListener("keydown", e => {
  for (let i = 0; i < inputs.length; i++) inputs[i].blur();
  if (guiParameters.isFirst && !control.isLocked && control.lock) control.lock()
  switch (e.code) {
    case "KeyW":
      cameraMove.W = true
      break;
    case "KeyA":
      cameraMove.A = true
      break;
    case "KeyS":
      cameraMove.S = true
      break;
    case "KeyD":
      cameraMove.D = true
      break;
    case "Space":
      cameraMove.Space = true
      break;
    case "ShiftLeft":
      cameraMove.shift = true
      break;
    case "KeyE":
      cameraMove.E = true
      break;
    default:
      break;
  }
})
window.addEventListener("keyup", e => {
  switch (e.code) {
    case "KeyW":
      cameraMove.W = false
      break;
    case "KeyA":
      cameraMove.A = false
      break;
    case "KeyS":
      cameraMove.S = false
      break;
    case "KeyD":
      cameraMove.D = false
      break;
    case "Space":
      cameraMove.Space = false
      break;
    case "ShiftLeft":
      cameraMove.shift = false
      break;
    case "KeyE":
      cameraMove.E = false
      break;
    default:
      break;
  }
})
window.addEventListener("resize", () => {
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.setPixelRatio(window.devicePixelRatio);
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
});
window.addEventListener("click", () => { if (guiParameters.isFirst && !control.isLocked && control.lock) control.lock() })

function createCanvasSprites () {
  const createGhostTexture = () => {
    const canvas = document.createElement('canvas')
    const ctx = canvas.getContext('2d')
    // the body
    ctx.translate(-81, -84)

    // 
    const kongMingLight = () => {
      // 
      ctx.fillStyle = '#e63b20'
      ctx.beginPath()
      ctx.moveTo(81, 116)
      ctx.lineTo(80, 102)
      ctx.lineTo(106, 102)
      ctx.lineTo(105, 116)
      ctx.bezierCurveTo(105, 112, 81, 112, 81, 116)
      ctx.lineTo(81, 116)
      ctx.fill()

      // 
      ctx.fillStyle = "#f7ea60";
      ctx.beginPath();
      ctx.moveTo(105, 116);
      ctx.bezierCurveTo(105, 110, 81, 110, 81, 116)
      ctx.lineTo(81, 116)
      ctx.fill()

      // 
      ctx.fillStyle = "#fefeff";
      ctx.beginPath();
      ctx.moveTo(95, 116);
      ctx.bezierCurveTo(95, 112, 91, 112, 91, 116)
      ctx.lineTo(91, 116)
      ctx.fill()
    }
    kongMingLight()

    const texture = new THREE.Texture(canvas)
    texture.needsUpdate = true
    return texture
  }
  const createCloud = (size, transparent, opacity, sizeAttenuation, color) => {
    const geom = new THREE.BufferGeometry()
    const material = new THREE.PointsMaterial({
      'size': size,
      'transparent': transparent,
      'opacity': opacity,
      'map': createGhostTexture(),
      'sizeAttenuation': sizeAttenuation,
      'color': color
    })
    let veticsFloat32Array = []
    const range = 500
    for (let i = 0; i < 1000; i++) {
      const particle = new THREE.Vector3(Math.random() * range - range / 2, Math.random() * range - range / 2, Math.random() * range - range / 2)
      veticsFloat32Array.push(particle.x, particle.y, particle.z)
    }
    const vertices = new THREE.Float32BufferAttribute(veticsFloat32Array, 3)
    geom.attributes.position = vertices
    const cloud = new THREE.Points(geom, material)
    scene.add(cloud)
  }
  createCloud(30, true, 0.9, true, 0xffffff)
}

let isTouch = false;
const touchMove = { X: 0, Y: 0 };
const direction = [0, 0, 0]; // 
const radius = 35 // 
const control_plate = document.querySelector(".control_plate")
const controlbtn = document.querySelector(".control")
const initPos = getElementOffset(controlbtn)

function onBtnTouchStart(e) {
  const { clientX, clientY } = e
  isTouch = true
  touchMove.X = clientX
  touchMove.Y = clientY
  control_plate.style.opacity = 1
}

function onBtnTouchMove(e) {
  const { clientX, clientY } = e
  const moveX = clientX - touchMove.X
  const moveY = clientY - touchMove.Y

  const btnOffset = getElementOffset(controlbtn)
  
  // 
  const total = Math.abs(moveX) + Math.abs(moveY)
  // direction[0] = moveX / total
  direction[0] = btnOffset.left - initPos.left
  direction[1] = 0
  // direction[2] = moveY / total
  direction[2] = btnOffset.top - initPos.top
  
  const signX = moveX > 0 ? true : false
  const signY = moveY > 0 ? true : false
  let btnPos = { x: 0, y: 0 }
  
  /**
   * 
   * x^2 + y^2 = r^2
   * x = rcos   y = rsin
   * cos = (1, 0)  (x, y) = x
   */
  const lenX = radius * Math.abs(moveX / total)
  const lenY = Math.sqrt(radius * radius - lenX * lenX)
  if (Math.abs(moveX) > lenX) {
    btnPos.x = signX ? lenX : -lenX
  } else {
    btnPos.x = moveX
  }
  if (Math.abs(moveY) > lenY) {
    btnPos.y = signY ? lenY : -lenY
  } else {
    btnPos.y = moveY
  }
  // 
  controlbtn.style.top = btnPos.y + 'px'
  controlbtn.style.left = btnPos.x + 'px'
}
/**
 *  y-
 *  y+
 *  x- 
 *  x+
 */
function onBtnTouchEnd() {
  isTouch = false
  controlbtn.style.top = 0 + 'px'
  controlbtn.style.left = 0 + 'px'
  control_plate.style.opacity = 0;
  for (let i = 0; i < direction.length; i++) {
    direction[i] = 0;
  }
}

function getElementOffset(element) {
  let offset = { left: 0, top: 0 }
  let current = element.offsetParent

  offset.left += element.offsetLeft
  offset.top += element.offsetTop

  while (current !== null) {
    offset.left += current.offsetLeft
    offset.top += current.offsetTop
    current = current.offsetParent
  }
  return offset
}

window.addEventListener("touchstart", e => {
  for (let i = 0; i < e.touches.length; i++) {
    const touchEvt = e.touches[i];
    if (touchEvt.target.className == "control") {
      onBtnTouchStart(touchEvt)
    }
    if (touchEvt.target.className == "jumpBtn") {
      cameraMove.Space = true
    }
  }
})
window.addEventListener("touchmove", e => {
  // console.log(e);
  for (let i = 0; i < e.touches.length; i++) {
    const touchEvt = e.touches[i];
    if (touchEvt.target.className == "control") {
      onBtnTouchMove(touchEvt)
    }
  }
})
window.addEventListener("touchend", e => {
  onBtnTouchEnd()
  cameraMove.Space = false
})